*{
  margin: 0;
  padding: 0;
}
.navBar{
  width: 100%;
  background-color: burlywood;
  height: 6vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 2rem; 
}
.Icon{
  margin-right: 0.8rem;
  color: white;
}
.logoName{
  color: white;
}
.container{
  width: 100%;
  height: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main{
  /* min-width: 30rem; */
  height:15rem;
  background-color:rgba(245, 245, 245, 0.89);
  text-align: justify;
  display: flex;
  flex-direction: column; 
  padding: 0.6rem;
  box-shadow: 5px 5px 20px -15px black;
  margin-top: 2rem;
}

.main1{
    min-width: 21rem;
    max-width: 21rem;
    min-height: 10rem;
    max-height: auto;
    background-color:rgba(245, 245, 245, 0.89); 
    display: flex;
    flex-direction: column; 
    padding: 1rem ; 
    box-shadow: 5px 5px 20px -15px black;
    margin:2rem 1rem 1.6rem 1rem;
    float: left;
}
.inp1,.inp2{
  border: none;
  font-size: 20px;
  border-bottom:2px rgb(228, 225, 225) solid;
  margin: 0.8rem 0 1rem 0;
  background-color:rgba(245, 245, 245, 0.89);
}
.inp11,.inp22{
  border: none;
  font-size: 20px;
  border-bottom:2px rgb(228, 225, 225) solid;
  margin: 0.8rem 0 1rem 0;
  background-color:rgba(245, 245, 245, 0.89);
  
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color:black;
  color: white;
  text-align: center;
}
.footer p{
  margin-top: 2rem;
}
.copy{
  margin-right: 0.6rem;
}
.btn1{
  width: 2.5rem;
  height: 2.5rem;
  margin: 0.5rem;
  background-color: none;
  border:none;
  border-radius: 50%;
  align-self: flex-end;
  font-weight: bold;
  box-shadow: 2px 2px 20px -5px black;
}
.btn1:hover{
  cursor: pointer;
  color: purple;
  background-color: cornflowerblue;
}
